import { useState, useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible";
import { ChevronLeft, ChevronRight, X, ImageIcon, ChevronDown, LayoutGrid, Square } from "lucide-react";
import { useDishImages } from "@/hooks/useDishImages";
import { useIsMobile } from "@/hooks/use-mobile";
import ImagePreviewModal from "@/components/ImagePreviewModal";

interface RestaurantDishGalleryProps {
  restaurantId: string;
  showGallery?: boolean;
}

const GALLERY_VIEW_KEY = "dish-gallery-view-mode";

export function RestaurantDishGallery({ restaurantId, showGallery = true }: RestaurantDishGalleryProps) {
  const { t } = useTranslation();
  const { data: images = [], isLoading } = useDishImages(restaurantId);
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const isMobile = useIsMobile();
  
  // View mode: 'single' or 'multi' - only for mobile
  const [viewMode, setViewMode] = useState<'single' | 'multi'>(() => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem(GALLERY_VIEW_KEY);
      return (saved === 'single' || saved === 'multi') ? saved : 'multi';
    }
    return 'multi';
  });

  // Persist view mode preference
  useEffect(() => {
    localStorage.setItem(GALLERY_VIEW_KEY, viewMode);
  }, [viewMode]);

  const openLightbox = useCallback((index: number) => {
    setCurrentIndex(index);
    setLightboxOpen(true);
  }, []);

  const closeLightbox = useCallback(() => {
    setLightboxOpen(false);
  }, []);

  const goToPrevious = useCallback(() => {
    setCurrentIndex((prev) => (prev === 0 ? images.length - 1 : prev - 1));
  }, [images.length]);

  const goToNext = useCallback(() => {
    setCurrentIndex((prev) => (prev === images.length - 1 ? 0 : prev + 1));
  }, [images.length]);

  // Handle keyboard navigation
  const handleKeyDown = useCallback((e: React.KeyboardEvent) => {
    if (e.key === "ArrowLeft") goToPrevious();
    if (e.key === "ArrowRight") goToNext();
    if (e.key === "Escape") closeLightbox();
  }, [goToPrevious, goToNext, closeLightbox]);

  if (isLoading) {
    return null;
  }

  // Don't render if gallery is disabled or no images
  if (!showGallery || !images.length) {
    return null;
  }

  // Determine basis class based on view mode (mobile only)
  const getCarouselItemClass = () => {
    if (isMobile && viewMode === 'single') {
      return "pl-2 basis-full";
    }
    return "pl-2 md:pl-4 basis-1/2 md:basis-1/3 lg:basis-1/4";
  };

  return (
    <>
      <Card>
        <Collapsible open={!isCollapsed} onOpenChange={(open) => setIsCollapsed(!open)}>
          <CardHeader className="pb-3">
            <CollapsibleTrigger className="w-full">
              <div className="flex items-center justify-between">
                <CardTitle className="text-lg flex items-center gap-2">
                  <ImageIcon className="w-5 h-5" />
                  {t("restaurant.photoGallery")}
                  <span className="text-sm font-normal text-muted-foreground">
                    ({images.length})
                  </span>
                </CardTitle>
                <div className="flex items-center gap-2">
                  {/* Mobile-only view mode toggle */}
                  {isMobile && (
                    <div 
                      className="flex items-center gap-1 bg-muted rounded-md p-0.5"
                      onClick={(e) => e.stopPropagation()}
                    >
                      <Button
                        variant={viewMode === 'single' ? 'default' : 'ghost'}
                        size="icon"
                        className="h-7 w-7"
                        onClick={(e) => {
                          e.stopPropagation();
                          setViewMode('single');
                        }}
                        title={t("dishImages.singleView")}
                      >
                        <Square className="h-4 w-4" />
                      </Button>
                      <Button
                        variant={viewMode === 'multi' ? 'default' : 'ghost'}
                        size="icon"
                        className="h-7 w-7"
                        onClick={(e) => {
                          e.stopPropagation();
                          setViewMode('multi');
                        }}
                        title={t("dishImages.multiView")}
                      >
                        <LayoutGrid className="h-4 w-4" />
                      </Button>
                    </div>
                  )}
                  <ChevronDown className={`w-5 h-5 text-muted-foreground transition-transform ${!isCollapsed ? 'rotate-180' : ''}`} />
                </div>
              </div>
            </CollapsibleTrigger>
          </CardHeader>
          <CollapsibleContent>
            <CardContent>
              <Carousel className="w-full">
                <CarouselContent className="-ml-2 md:-ml-4">
                  {images.map((image, index) => (
                    <CarouselItem
                      key={image.id}
                      className={getCarouselItemClass()}
                    >
                      <div
                        className="aspect-square rounded-lg overflow-hidden cursor-pointer hover:opacity-90 transition-opacity"
                        onClick={() => openLightbox(index)}
                      >
                        <img
                          src={image.image_url}
                          alt={`Dish ${index + 1}`}
                          className="w-full h-full object-cover"
                          loading="lazy"
                        />
                      </div>
                    </CarouselItem>
                  ))}
                </CarouselContent>
                {images.length > (isMobile && viewMode === 'single' ? 1 : 2) && (
                  <>
                    <CarouselPrevious className="-left-3 md:-left-4" />
                    <CarouselNext className="-right-3 md:-right-4" />
                  </>
                )}
              </Carousel>
              <p className="text-sm text-muted-foreground mt-2 text-center">
                {t("dishImages.clickToEnlarge")}
              </p>
            </CardContent>
          </CollapsibleContent>
        </Collapsible>
      </Card>

      {/* Image Preview with Navigation */}
      {lightboxOpen && (
        <>
          <ImagePreviewModal
            imageUrl={images[currentIndex]?.image_url || ""}
            isOpen={lightboxOpen}
            onClose={closeLightbox}
          />
          {/* Navigation overlay for multi-image gallery */}
          {images.length > 1 && (
            <div className="fixed inset-0 z-[101] pointer-events-none">
              {/* Previous button */}
              <Button
                variant="ghost"
                size="icon"
                className="absolute left-2 top-1/2 -translate-y-1/2 z-10 text-white hover:bg-white/20 h-12 w-12 pointer-events-auto"
                onClick={(e) => {
                  e.stopPropagation();
                  goToPrevious();
                }}
              >
                <ChevronLeft className="w-8 h-8" />
              </Button>

              {/* Next button */}
              <Button
                variant="ghost"
                size="icon"
                className="absolute right-2 top-1/2 -translate-y-1/2 z-10 text-white hover:bg-white/20 h-12 w-12 pointer-events-auto"
                onClick={(e) => {
                  e.stopPropagation();
                  goToNext();
                }}
              >
                <ChevronRight className="w-8 h-8" />
              </Button>

              {/* Image counter */}
              <div className="absolute bottom-4 left-1/2 -translate-x-1/2 text-white bg-black/50 px-3 py-1 rounded-full text-sm pointer-events-auto">
                {currentIndex + 1} / {images.length}
              </div>
            </div>
          )}
        </>
      )}
    </>
  );
}