import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Play, Film } from "lucide-react";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { AspectRatio } from "@/components/ui/aspect-ratio";
import { GhostRestaurantVideoModal } from "@/components/GhostRestaurantVideoModal";
import { useIsMobile } from "@/hooks/use-mobile";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";

interface VideoItem {
  url: string;
  title_zh?: string;
  title_en?: string;
  title_pt?: string;
  poster_url?: string;
  duration?: number;
}

interface GhostRestaurantVideosProps {
  videos: VideoItem[];
}

const formatDuration = (seconds?: number): string => {
  if (!seconds) return "";
  const mins = Math.floor(seconds / 60);
  const secs = Math.floor(seconds % 60);
  return `${mins}:${secs.toString().padStart(2, '0')}`;
};

const VideoCard = ({ 
  video, 
  onClick,
  language 
}: { 
  video: VideoItem; 
  onClick: () => void;
  language: string;
}) => {
  const getLocalizedTitle = () => {
    switch (language) {
      case 'en':
        return video.title_en || video.title_zh || '';
      case 'pt':
        return video.title_pt || video.title_en || video.title_zh || '';
      default:
        return video.title_zh || video.title_en || '';
    }
  };

  const title = getLocalizedTitle();

  return (
    <Card 
      className="group cursor-pointer overflow-hidden border-border/50 hover:border-primary/30 transition-all hover:shadow-lg"
      onClick={onClick}
    >
      <div className="relative">
        <AspectRatio ratio={16/9}>
          {video.poster_url ? (
            // 
            <img 
              src={video.poster_url} 
              alt={title}
              className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-105"
              loading="lazy"
            />
          ) : (
            // 
            <div className="w-full h-full bg-gradient-to-br from-primary/20 via-primary/10 to-muted flex items-center justify-center">
              <div className="w-16 h-16 rounded-full bg-primary/20 backdrop-blur-sm flex items-center justify-center border border-primary/30">
                <Play className="w-8 h-8 text-primary ml-1" fill="currentColor" />
              </div>
              <Film className="absolute bottom-3 left-3 w-5 h-5 text-primary/40" />
            </div>
          )}
        </AspectRatio>

        {/*  */}
        <div className="absolute inset-0 flex items-center justify-center pointer-events-none md:opacity-0 md:group-hover:opacity-100 transition-opacity">
          <div className="w-12 h-12 md:w-14 md:h-14 rounded-full bg-black/50 md:bg-white/20 backdrop-blur-sm flex items-center justify-center border border-white/30 md:group-hover:scale-110 transition-transform">
            <Play className="w-6 h-6 md:w-7 md:h-7 text-white ml-0.5" fill="white" />
          </div>
        </div>

        {/*  */}
        {video.duration && (
          <Badge 
            variant="secondary" 
            className="absolute bottom-2 right-2 bg-black/70 text-white text-xs border-0"
          >
            {formatDuration(video.duration)}
          </Badge>
        )}
      </div>

      {/*  */}
      {title && (
        <div className="p-3">
          <p className="text-sm font-medium text-foreground line-clamp-2">{title}</p>
        </div>
      )}
    </Card>
  );
};

export const GhostRestaurantVideos = ({ videos }: GhostRestaurantVideosProps) => {
  const { t, i18n } = useTranslation();
  const isMobile = useIsMobile();
  const [selectedVideo, setSelectedVideo] = useState<VideoItem | null>(null);

  if (!videos || videos.length === 0) {
    return null;
  }

  const getLocalizedVideoTitle = (video: VideoItem) => {
    switch (i18n.language) {
      case 'en':
        return video.title_en || video.title_zh || '';
      case 'pt':
        return video.title_pt || video.title_en || video.title_zh || '';
      default:
        return video.title_zh || video.title_en || '';
    }
  };

  return (
    <>
      <section className="py-8 md:py-12 lg:py-16 bg-muted/30">
        <div className="container mx-auto max-w-6xl px-4">
          {/* Section Header */}
          <div className="text-center mb-6 md:mb-8 lg:mb-10">
            <p className="text-[10px] md:text-xs font-medium tracking-[0.2em] md:tracking-[0.25em] uppercase text-primary mb-2 md:mb-3">
              BEHIND THE KITCHEN
            </p>
            <h2 className="text-xl md:text-2xl lg:text-3xl font-bold text-foreground flex items-center justify-center gap-2 md:gap-3">
              <Play className="w-5 h-5 md:w-6 md:h-6 lg:w-7 lg:h-7 text-primary" />
              {t("restaurants.videoSection", "")}
            </h2>
            <div className="w-12 md:w-16 h-0.5 md:h-1 bg-primary mx-auto mt-3 md:mt-4" />
          </div>

          {/* Video Grid/Carousel */}
          {isMobile ? (
            <Carousel
              opts={{
                align: "start",
                loop: videos.length > 2,
              }}
              className="w-full"
            >
              <CarouselContent className="-ml-2">
                {videos.map((video, index) => (
                  <CarouselItem key={index} className="pl-2 basis-4/5 sm:basis-1/2">
                    <VideoCard 
                      video={video} 
                      onClick={() => setSelectedVideo(video)}
                      language={i18n.language}
                    />
                  </CarouselItem>
                ))}
              </CarouselContent>
              {videos.length > 2 && (
                <>
                  <CarouselPrevious className="hidden sm:flex -left-4" />
                  <CarouselNext className="hidden sm:flex -right-4" />
                </>
              )}
            </Carousel>
          ) : (
            <div className="grid grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
              {videos.map((video, index) => (
                <VideoCard 
                  key={index}
                  video={video} 
                  onClick={() => setSelectedVideo(video)}
                  language={i18n.language}
                />
              ))}
            </div>
          )}
        </div>
      </section>

      {/* Video Player Modal */}
      <GhostRestaurantVideoModal
        open={!!selectedVideo}
        onOpenChange={(open) => !open && setSelectedVideo(null)}
        videoUrl={selectedVideo?.url || ''}
        posterUrl={selectedVideo?.poster_url}
        title={selectedVideo ? getLocalizedVideoTitle(selectedVideo) : undefined}
      />
    </>
  );
};

export default GhostRestaurantVideos;