import * as DialogPrimitive from "@radix-ui/react-dialog";
import { VisuallyHidden } from "@radix-ui/react-visually-hidden";
import { Button } from "@/components/ui/button";
import { X, Volume2, VolumeX, Maximize, Minimize, Play, Pause } from "lucide-react";
import { useRef, useState, useEffect, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { cn } from "@/lib/utils";

interface GhostRestaurantVideoModalProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  videoUrl: string;
  posterUrl?: string;
  title?: string;
}

export const GhostRestaurantVideoModal = ({
  open,
  onOpenChange,
  videoUrl,
  posterUrl,
  title,
}: GhostRestaurantVideoModalProps) => {
  const { t } = useTranslation();
  const videoRef = useRef<HTMLVideoElement>(null);
  const progressRef = useRef<HTMLDivElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [showControls, setShowControls] = useState(true);
  const controlsTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const handlePlayPause = useCallback(() => {
    if (!videoRef.current) return;
    if (isPlaying) {
      videoRef.current.pause();
    } else {
      videoRef.current.play();
    }
  }, [isPlaying]);

  const handleMuteToggle = useCallback(() => {
    if (!videoRef.current) return;
    videoRef.current.muted = !isMuted;
    setIsMuted(!isMuted);
  }, [isMuted]);

  const handleFullscreenToggle = useCallback(async () => {
    try {
      if (!document.fullscreenElement) {
        await videoRef.current?.requestFullscreen();
        setIsFullscreen(true);
      } else {
        await document.exitFullscreen();
        setIsFullscreen(false);
      }
    } catch (error) {
      console.log('Fullscreen not supported:', error);
    }
  }, []);

  const handleProgressClick = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
    if (!videoRef.current || !progressRef.current) return;
    const rect = progressRef.current.getBoundingClientRect();
    const clickX = e.clientX - rect.left;
    const percentage = clickX / rect.width;
    videoRef.current.currentTime = percentage * duration;
  }, [duration]);

  const showControlsTemporarily = useCallback(() => {
    setShowControls(true);
    if (controlsTimeoutRef.current) {
      clearTimeout(controlsTimeoutRef.current);
    }
    controlsTimeoutRef.current = setTimeout(() => {
      if (isPlaying) {
        setShowControls(false);
      }
    }, 3000);
  }, [isPlaying]);

  const handleProgressTouch = useCallback((e: React.TouchEvent<HTMLDivElement>) => {
    if (!videoRef.current || !progressRef.current) return;
    e.preventDefault();
    e.stopPropagation();
    const rect = progressRef.current.getBoundingClientRect();
    const touchX = e.touches[0].clientX - rect.left;
    const percentage = Math.max(0, Math.min(1, touchX / rect.width));
    videoRef.current.currentTime = percentage * duration;
  }, [duration]);

  // Reset state when modal opens/closes
  useEffect(() => {
    if (open && videoRef.current) {
      // Reset video to start when opening
      videoRef.current.currentTime = 0;
      setCurrentTime(0);
      setShowControls(true);
      
      // Auto-play when modal opens
      videoRef.current.play().catch(() => {
        // Autoplay blocked, show play button
        setIsPlaying(false);
      });
    } else if (!open && videoRef.current) {
      // Pause and reset when closing
      videoRef.current.pause();
      videoRef.current.currentTime = 0;
      setIsPlaying(false);
      setCurrentTime(0);
      setShowControls(true);
    }
    
    return () => {
      if (controlsTimeoutRef.current) {
        clearTimeout(controlsTimeoutRef.current);
      }
    };
  }, [open]);

  // Video event handlers
  const handlePlay = useCallback(() => setIsPlaying(true), []);
  const handlePauseEvent = useCallback(() => setIsPlaying(false), []);
  const handleTimeUpdate = useCallback(() => {
    if (videoRef.current) {
      setCurrentTime(videoRef.current.currentTime);
    }
  }, []);
  const handleLoadedMetadata = useCallback(() => {
    if (videoRef.current) {
      setDuration(videoRef.current.duration);
    }
  }, []);
  const handleEnded = useCallback(() => {
    setIsPlaying(false);
    setShowControls(true);
  }, []);

  const progress = duration > 0 ? (currentTime / duration) * 100 : 0;

  return (
    <DialogPrimitive.Root open={open} onOpenChange={onOpenChange}>
      <DialogPrimitive.Portal>
        {/* Overlay - Lower z-index, pointer-events-none to not block clicks */}
        <DialogPrimitive.Overlay 
          className="fixed inset-0 z-[49] bg-black/90 data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0"
          style={{ pointerEvents: 'none' }}
        />
        
        {/* Content Container - Higher z-index for proper layering */}
        <div 
          className="fixed inset-0 z-50 flex items-center justify-center p-4"
          style={{ touchAction: 'none' }}
          onClick={(e) => {
            // Only close if clicking the backdrop, not the content
            if (e.target === e.currentTarget) {
              onOpenChange(false);
            }
          }}
          onTouchEnd={(e) => {
            // Close on tap backdrop
            if (e.target === e.currentTarget) {
              onOpenChange(false);
            }
          }}
        >
          <DialogPrimitive.Content 
            className="relative w-full max-w-4xl bg-black rounded-lg overflow-hidden outline-none"
            style={{ touchAction: 'auto' }}
            aria-describedby={undefined}
            onMouseMove={showControlsTemporarily}
            onTouchStart={(e) => {
              e.stopPropagation();
              showControlsTemporarily();
            }}
            onTouchMove={(e) => {
              e.stopPropagation();
              showControlsTemporarily();
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Accessibility: Hidden title for screen readers */}
            <VisuallyHidden>
              <DialogPrimitive.Title>{title || t("restaurants.videoPlayer", "Video Player")}</DialogPrimitive.Title>
            </VisuallyHidden>
            {/* Close Button */}
            <Button
              type="button"
              variant="ghost"
              size="icon"
              className={cn(
                "absolute top-3 right-3 z-[60] bg-black/50 hover:bg-black/70 text-white h-10 w-10 rounded-full transition-opacity duration-300",
                showControls ? "opacity-100" : "opacity-0 pointer-events-none"
              )}
              onClick={(e) => {
                e.stopPropagation();
                onOpenChange(false);
              }}
            >
              <X className="h-5 w-5" />
            </Button>

            {/* Title */}
            {title && (
              <div className={cn(
                "absolute top-3 left-3 z-[60] transition-opacity duration-300 pointer-events-none",
                showControls ? "opacity-100" : "opacity-0"
              )}>
                <h3 className="text-white font-semibold text-lg drop-shadow-lg">{title}</h3>
              </div>
            )}

            {/* Video Player */}
            <div className="relative aspect-video bg-black select-none">
              <video
                ref={videoRef}
                src={videoUrl}
                poster={posterUrl}
                className="w-full h-full object-contain cursor-pointer"
                playsInline
                preload="metadata"
                onPlay={handlePlay}
                onPause={handlePauseEvent}
                onTimeUpdate={handleTimeUpdate}
                onLoadedMetadata={handleLoadedMetadata}
                onEnded={handleEnded}
                onCanPlay={() => {
                  // Ensure duration is set when video can play
                  if (videoRef.current && !duration) {
                    setDuration(videoRef.current.duration);
                  }
                }}
                onClick={(e) => {
                  e.stopPropagation();
                  handlePlayPause();
                }}
                onTouchEnd={(e) => {
                  // Only toggle play/pause if not dragging progress bar
                  if (e.target === videoRef.current) {
                    e.preventDefault();
                    handlePlayPause();
                  }
                }}
              />

              {/* Play Overlay - Only show when paused, positioned above video but below controls */}
              {!isPlaying && (
                <div 
                  className="absolute inset-0 flex items-center justify-center cursor-pointer bg-black/30"
                  style={{ zIndex: 5 }}
                  onClick={(e) => {
                    e.stopPropagation();
                    handlePlayPause();
                  }}
                  onTouchEnd={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    handlePlayPause();
                  }}
                >
                  <div className="w-16 h-16 md:w-20 md:h-20 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center hover:bg-white/30 transition-colors">
                    <Play className="w-8 h-8 md:w-10 md:h-10 text-white ml-1" fill="white" />
                  </div>
                </div>
              )}

              {/* Controls Bar - Always capture pointer events when visible */}
              <div 
                className={cn(
                  "absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 to-transparent px-4 py-3 transition-opacity duration-300",
                  showControls ? "opacity-100" : "opacity-0"
                )}
                style={{ 
                  zIndex: 30,
                  pointerEvents: showControls ? 'auto' : 'none'
                }}
                onClick={(e) => e.stopPropagation()}
                onTouchStart={(e) => e.stopPropagation()}
                onTouchMove={(e) => e.stopPropagation()}
              >
                {/* Progress Bar - Larger touch target */}
                <div 
                  ref={progressRef}
                  className="w-full py-2 cursor-pointer mb-2 group"
                  style={{ touchAction: 'none' }}
                  onClick={(e) => {
                    e.stopPropagation();
                    handleProgressClick(e);
                  }}
                  onTouchStart={(e) => {
                    e.stopPropagation();
                    handleProgressTouch(e);
                  }}
                  onTouchMove={(e) => {
                    e.stopPropagation();
                    handleProgressTouch(e);
                  }}
                  onTouchEnd={(e) => {
                    e.stopPropagation();
                  }}
                >
                  <div className="w-full h-2 bg-white/30 rounded-full relative">
                    <div 
                      className="h-full bg-primary rounded-full relative transition-all"
                      style={{ width: `${progress}%` }}
                    >
                      <div className="absolute right-0 top-1/2 -translate-y-1/2 w-5 h-5 bg-white rounded-full shadow-md -mr-2.5" />
                    </div>
                  </div>
                </div>

                {/* Control Buttons */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="h-10 w-10 text-white hover:bg-white/20 active:scale-95"
                      onClick={(e) => {
                        e.stopPropagation();
                        handlePlayPause();
                      }}
                      onTouchEnd={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handlePlayPause();
                      }}
                    >
                      {isPlaying ? <Pause className="h-6 w-6" /> : <Play className="h-6 w-6" />}
                    </Button>

                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="h-10 w-10 text-white hover:bg-white/20 active:scale-95"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleMuteToggle();
                      }}
                      onTouchEnd={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleMuteToggle();
                      }}
                    >
                      {isMuted ? <VolumeX className="h-6 w-6" /> : <Volume2 className="h-6 w-6" />}
                    </Button>

                    <span className="text-white text-sm font-medium select-none">
                      {formatTime(currentTime)} / {formatTime(duration)}
                    </span>
                  </div>

                  <Button
                    type="button"
                    variant="ghost"
                    size="icon"
                    className="h-10 w-10 text-white hover:bg-white/20 active:scale-95"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleFullscreenToggle();
                    }}
                    onTouchEnd={(e) => {
                      e.preventDefault();
                      e.stopPropagation();
                      handleFullscreenToggle();
                    }}
                  >
                    {isFullscreen ? <Minimize className="h-6 w-6" /> : <Maximize className="h-6 w-6" />}
                  </Button>
                </div>
              </div>
            </div>
          </DialogPrimitive.Content>
        </div>
      </DialogPrimitive.Portal>
    </DialogPrimitive.Root>
  );
};

export default GhostRestaurantVideoModal;
